<template>
	<div id="wrap">
		<ul class="list" v-show='show' v-if='data.data'>
			<li v-for='item,index in data.data.song.itemlist' @click='go(item)'>
				<span>{{item.name}}</span>-
				<span>{{item.singer}}</span>
			</li>
		</ul>
	</div>
</template>
<script>
	import axios from 'axios'
	export default{
		data(){
			return {
				data: {}
			}
		},
		props: {
			show: {}
		},
		methods: {
			go(item){
        let arr = localStorage.lishi || [];
        if(arr.length){arr = JSON.parse(arr)}
        let str = item.name + ' ' + item.singer;
        if(arr.indexOf(str)==-1){
          arr.unshift(str);
        }
        localStorage.lishi = JSON.stringify(arr);
				/* this.$router.push({
					path: '/deta'
				}) */
			}
		},
		watch: {
			show(){
				axios.get(`/qq/splcloud/fcgi-bin/smartbox_new.fcg?is_xml=0&key=${this.show}&g_tk=5381&loginUin=0&hostUin=0&format=json&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq.json&needNewCode=0`).then(data => {
					console.log(data.data)
					this.data = data.data;
				})
			}
		}
	}
</script>
<style scoped>
	.list li{
		height: 35px;
		line-height: 35px;
		color: rgb(82,82,82);
		overflow:hidden;
	    text-overflow:ellipsis;
	    white-space:nowrap
	}
</style>



