<template>
	<div id="box">
		<input1 @ups='ups' ref='show'></input1>
    <div v-if='!show'>
      <h4>热门搜索</h4>
      <ul class='list'  v-if='data.data'>
        <li v-for='item,index in data.data.hotkey' @click='fn2(item.k)' v-if='index<10'>{{item.k}}</li>
      </ul>
      <h4>搜索历史</h4>
      <ul class="lishi">
        <li v-for='item,index in lishi' :key='index' v-if='index<10'>{{item}}</li>
      </ul>
    </div>
		<searchlist :show='show'></searchlist>
	</div>
</template>
<script>
	import loading from './loading.vue';
	import input1 from './input1.vue';
	import searchlist from './searchlist.vue';
	import axios from 'axios';
	export default {
		data(){
			return{
				show: '',
				data: {},
        lishi: []
			}
		},
		components: {
			input1,
			searchlist
		},
		mounted(){
			axios.get('/qq/splcloud/fcgi-bin/gethotkey.fcg?g_tk=5381&loginUin=0&hostUin=0&format=json&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq.json&needNewCode=0').then(data => {
				this.data = data.data;
			})
      this.lishi = localStorage.lishi || [];
      if(this.lishi.length){this.lishi = JSON.parse(this.lishi)};
		},
		methods: {
			ups(val){
				this.show = val;
			},
			fn2(k){
				this.$refs.show.val = k;
			}
		}
	}
</script>
<style scoped>
	#box{
		height: 85vh;
		background:rgb(39,39,39);
		padding-left: 35px;
	}
	h4{
		color: rgb(104,104,104);
	}
	.list{
		display: flex;
		flex-wrap: wrap;
		margin-top: 20px;
	}
	.list li{
		display: inline-block;
		padding: 0 8px;
		height: 25px;
		text-align: center;
		line-height: 25px;
		color: rgb(104,104,104);
		background: rgb(53,53,53);
		border-radius: 5px;
		margin-right: 10px;
		margin-bottom: 10px;
	}
  .lishi{
    margin-top: 20px;
  }
  .lishi li{
    padding: 0 8px;
    height: 25px;
    text-align: center;
    line-height: 25px;
    color: rgb(104,104,104);
    background: rgb(53,53,53);
    border-radius: 5px;
    margin-right: 10px;
    margin-bottom: 10px;
  }
</style>



