<template>
	<div id="wrap">
		<i class='icon'></i>
		<input type="text" v-model='val' @keyup='fn' placeholder='搜索歌曲、歌手'>
		<span v-show='val' @click='clear'>X</span>
	</div>
</template>
<script>
	import axios from 'axios';
	export default{
		data(){
			return{
				val: ''
			}
		},
		props: {
			k: {}
		},
		methods: {
			fn(){
				
			},
			clear(){
				this.val = '';
			}
		},
		watch: {
			val(newVal){
				this.$emit('ups',newVal);
			}
		}
	}
</script>
<style scoped>
	#wrap{
		background: rgb(39,39,39);
		height: 90px;
		position: relative;
	}
	.icon{
		display: inline-block;
		width: 22px;
		height: 21px;
		background: url('/static/images/search.png');
		background-size: 22px 21px;
		position: absolute;
		top: 28px;
		left: 10px;
	}
	input{
		background: rgb(53,53,53);
		width: 80vw;
		height: 35px;
		border-radius: 10px;
		margin-top: 20px;
		border: none;
		color: #fff;
		text-indent: 45px;
		outline:none;
	}
	span{
		color: #fff;
	}
</style>


